import { Button, Fade, InputLabel, MenuItem, Select, SelectChangeEvent, Snackbar, TextField } from "@mui/material";
import React, { useCallback, useEffect, useState } from "react";
import { ToWords } from "to-words";
import allwords from "./allwords.txt";
import "./App.css";
import words from "./words.txt";

const minWordLen = 4;
const maxWordLen = 7;

const toWords = new ToWords();

function App() {
  const wordsMap = getWordsMap(words as string, minWordLen, maxWordLen);
  const allwordsMap = getWordsMap(allwords as string, minWordLen, maxWordLen);
  removeWordsNotInAllwords(wordsMap, allwordsMap);
  return <Wordle wordsMap={wordsMap} allwordsMap={allwordsMap}></Wordle>;
}

function getWordsMap(wordsString: string, minLen: number, maxLen: number) {
  const wordsMap = new Map<number, string[]>();
  for (let i = minLen; i <= maxLen; i++) {
    wordsMap.set(i, []);
  }
  const listOfWords = wordsString.split(/\s+/);
  for (const word of listOfWords) {
    if (word.length >= minLen && word.length <= maxLen) {
      wordsMap.get(word.length)!.push(word.toUpperCase());
    }
  }
  return wordsMap;
}

function removeWordsNotInAllwords(wordsMap: Map<number, string[]>, allwordsMap: Map<number, string[]>) {
  for (let i = minWordLen; i <= maxWordLen; i++) {
    const filteredWords: string[] = [];
    for (let j = 0; j < wordsMap.get(i)!.length; j++) {
      if (allwordsMap.get(i)!.includes(wordsMap.get(i)![j])) {
        filteredWords.push(wordsMap.get(i)![j]);
      }
    }
    wordsMap.set(i, filteredWords);
  }
}

const Wordle: React.FC<{ wordsMap: Map<number, string[]>; allwordsMap: Map<number, string[]> }> = ({
  wordsMap,
  allwordsMap,
}) => {
  const getInitialGuesses = (numGuesses: number, numCharacters: number) => {
    const guesses: Guess[] = [];
    for (let i = 0; i < numGuesses; i++) {
      const guessCharacters: GuessCharacter[] = [];
      for (let j = 0; j < numCharacters; j++) {
        guessCharacters.push({ character: "" });
      }
      guesses.push({ guessCharacters });
    }
    return guesses;
  };

  const getInitialCharacterStatusMap = () => {
    const characterStatusMap = new Map<string, CharacterStatus>();
    for (let ch = 0; ch < 26; ch++) {
      characterStatusMap.set(String.fromCharCode(65 + ch), "unknown");
    }
    return characterStatusMap;
  };

  const [numGuessesSetting, setNumGuessesSetting] = useState("6");
  const [numGuesses, setNumGuesses] = useState(6);
  const [numCharacters, setNumCharacters] = useState(5);
  const [guesses, setGuesses] = useState(getInitialGuesses(numGuesses, numCharacters));
  const [characterStatusMap, setCharacterStatusMap] = useState(getInitialCharacterStatusMap());
  const [word, setWord] = useState("");
  const [characterIndicesMap, setCharacterIndicesMap] = useState(new Map<string, Set<number>>());
  const [gameState, setGameState] = useState<GameState>("new");
  const [currentGuessIndex, setCurrentGuessIndex] = useState(0);
  const [currentCharacterIndex, setCurrentCharacterIndex] = useState(0);
  const [notificationOpen, setNotificationOpen] = useState(false);
  const [message, setMessage] = useState("");

  useEffect(() => {
    setGuesses(getInitialGuesses(numGuesses, numCharacters));
  }, [numGuesses, numCharacters]);

  useEffect(() => {
    if (gameState === "new") {
      setGuesses(getInitialGuesses(numGuesses, numCharacters));
      setCharacterStatusMap(getInitialCharacterStatusMap());
      setCurrentGuessIndex(0);
      setCurrentCharacterIndex(0);
    } else if (gameState === "in_progress") {
      const listOfWords = wordsMap.get(numCharacters)!;
      const randomWord = listOfWords[Math.floor(Math.random() * listOfWords.length)];
      setWord(randomWord);
      setCharacterIndicesMap(getCharacterIndicesMapForWord(randomWord));
    }
  }, [gameState]);

  const getCharacterIndicesMapForWord = (word: string) => {
    const characterIndicesMapForWord = new Map<string, Set<number>>();
    for (let i = 0; i < word.length; i++) {
      if (!characterIndicesMapForWord.has(word[i])) {
        characterIndicesMapForWord.set(word[i], new Set<number>());
      }
      characterIndicesMapForWord.get(word[i])!.add(i);
    }
    return characterIndicesMapForWord;
  };

  const handleBackspace = () => {
    const currentGuesses = [...guesses];
    currentGuesses[currentGuessIndex].guessCharacters[currentCharacterIndex - 1] = { character: "" };
    setGuesses(currentGuesses);
    setCurrentCharacterIndex(currentCharacterIndex - 1);
  };
  const handleEnter = () => {
    if (currentCharacterIndex !== numCharacters) {
      setMessage("Not enough letters");
      setNotificationOpen(true);
      return;
    }
    const guess = guesses[currentGuessIndex].guessCharacters.map((guessCharacter) => guessCharacter.character).join("");
    if (!allwordsMap.get(numCharacters)!.includes(guess)) {
      setMessage("Not in word list");
      setNotificationOpen(true);
      return;
    }
    updateCurrentGuessStatus();
    updateCharacterStatusMap();
    updateGameState();
    setCurrentGuessIndex(currentGuessIndex + 1);
    setCurrentCharacterIndex(0);
  };
  const updateCurrentGuessStatus = () => {
    for (let i = 0; i < numCharacters; i++) {
      if (word[i] === guesses[currentGuessIndex].guessCharacters[i].character) {
        guesses[currentGuessIndex].guessCharacters[i].status = "correct";
      } else if (characterIndicesMap.has(guesses[currentGuessIndex].guessCharacters[i].character)) {
        guesses[currentGuessIndex].guessCharacters[i].status = "wrong_position";
      } else {
        guesses[currentGuessIndex].guessCharacters[i].status = "not_used";
      }
    }
  };
  const updateCharacterStatusMap = () => {
    const updatedCharacterStatusMap = new Map(characterStatusMap);
    for (let i = 0; i < numCharacters; i++) {
      const currentCharacter = guesses[currentGuessIndex].guessCharacters[i];
      if (
        currentCharacter.status === "correct" ||
        currentCharacter.status === "not_used" ||
        updatedCharacterStatusMap.get(currentCharacter.character) === "unknown"
      ) {
        updatedCharacterStatusMap.set(currentCharacter.character, currentCharacter.status!);
      }
    }
    setCharacterStatusMap(updatedCharacterStatusMap);
  };
  const updateGameState = () => {
    const numCorrect = guesses[currentGuessIndex].guessCharacters.filter(
      (gameCharacter) => gameCharacter.status === "correct"
    ).length;
    if (numCorrect === numCharacters) {
      setGameState("won");
      setMessage("Splendid!");
      setNotificationOpen(true);
    } else if (currentGuessIndex === numGuesses - 1) {
      setGameState("lost");
      setMessage(word);
      setNotificationOpen(true);
    }
  };
  const handleAToZ = (event: KeyboardEvent) => {
    const currentGuesses = [...guesses];
    currentGuesses[currentGuessIndex].guessCharacters[currentCharacterIndex] = {
      character: event.key.toUpperCase(),
      status: "unknown",
    };
    setGuesses(currentGuesses);
    setCurrentCharacterIndex(currentCharacterIndex + 1);
  };

  const handleKeyupEvent = useCallback(
    (event: KeyboardEvent) => {
      if (gameState === "in_progress") {
        if (event.keyCode === 8 && currentCharacterIndex > 0) {
          handleBackspace();
        } else if (event.keyCode === 13) {
          handleEnter();
        } else if (event.keyCode >= 65 && event.keyCode <= 90 && currentCharacterIndex < numCharacters) {
          handleAToZ(event);
        }
      }
    },
    [gameState, currentGuessIndex, currentCharacterIndex]
  );

  useEffect(() => {
    document.addEventListener("keyup", handleKeyupEvent);

    return () => {
      document.removeEventListener("keyup", handleKeyupEvent);
    };
  }, [handleKeyupEvent]);

  const onGuessesChanged = (event: React.ChangeEvent<HTMLInputElement>) => {
    setNumGuessesSetting(event.target.value);
    const valueAsNumber = Number(event.target.value);
    if (!isNaN(valueAsNumber) && valueAsNumber === Math.round(valueAsNumber)) {
      setNumGuesses(valueAsNumber);
    }
  };
  const onCharactersChanged = (event: SelectChangeEvent<number>) => {
    setNumCharacters(event.target.value as number);
  };
  const share = (event: React.MouseEvent<HTMLButtonElement>) => {
    let result = "Wordle+: ";
    if (gameState === "won") {
      result += currentGuessIndex;
    } else if (gameState === "lost") {
      result += "X";
    }
    result += "/";
    result += numGuesses;
    result += "\n";
    for (let i = 0; i < currentGuessIndex; i++) {
      result += guesses[i].guessCharacters
        .map((guessCharacter) => {
          if (guessCharacter.status === "not_used") {
            return "";
          } else if (guessCharacter.status === "wrong_position") {
            return "";
          } else if (guessCharacter.status === "correct") {
            return "";
          }
          return "";
        })
        .join("");
      result += "\n";
    }
    navigator.clipboard.writeText(result);
    setMessage("Copied results to clipboard");
    setNotificationOpen(true);
    (event.target as HTMLButtonElement).blur();
  };
  const start = (event: React.MouseEvent<HTMLButtonElement>) => {
    setGameState("in_progress");
    (event.target as HTMLButtonElement).blur();
  };
  const reset = (event: React.MouseEvent<HTMLButtonElement>) => {
    setGameState("new");
    (event.target as HTMLButtonElement).blur();
  };

  return (
    <div className="wordle">
      <div className="content_container">
        <div className="title">WORDLE+</div>
        <ToolbarComponent
          gameState={gameState}
          numGuesses={numGuessesSetting}
          onGuessesChanged={onGuessesChanged}
          numCharacters={numCharacters}
          onCharactersChanged={onCharactersChanged}
          share={share}
          start={start}
          reset={reset}
        ></ToolbarComponent>
        <GuessesComponent guesses={guesses}></GuessesComponent>
      </div>
      <KeyboardComponent characterStatusMap={characterStatusMap}></KeyboardComponent>
      <Snackbar
        anchorOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
        autoHideDuration={1000}
        transitionDuration={{ enter: 0 }}
        TransitionComponent={Fade}
        open={notificationOpen}
        onClose={(_, reason) => {
          if (reason === "timeout") {
            setNotificationOpen(false);
          }
        }}
        sx={{ zIndex: notificationOpen ? 1400 : -1 }}
      >
        <div className="notification_message">{message}</div>
      </Snackbar>
    </div>
  );
};

type GameState = "new" | "in_progress" | "won" | "lost";

const ToolbarComponent: React.FC<{
  gameState: GameState;
  numGuesses: string;
  onGuessesChanged: (event: React.ChangeEvent<HTMLInputElement>) => void;
  numCharacters: number;
  onCharactersChanged: (event: SelectChangeEvent<number>) => void;
  share: (event: React.MouseEvent<HTMLButtonElement>) => void;
  start: (event: React.MouseEvent<HTMLButtonElement>) => void;
  reset: (event: React.MouseEvent<HTMLButtonElement>) => void;
}> = ({ gameState, numGuesses, onGuessesChanged, numCharacters, onCharactersChanged, share, start, reset }) => {
  return (
    <div className="toolbar">
      {gameState === "new" && (
        <div>
          <div className="num_guesses_setting">
            <InputLabel>Number of Guesses:</InputLabel>
            <TextField
              size="small"
              value={numGuesses}
              onChange={onGuessesChanged}
              sx={{ marginLeft: "8px", width: 50 }}
            ></TextField>
          </div>
          <div className="num_characters_setting">
            <InputLabel>Word Length:</InputLabel>
            <Select size="small" value={numCharacters} onChange={onCharactersChanged}>
              {Array.from({ length: maxWordLen - minWordLen + 1 }, (_, i) => i + minWordLen).map((i) => (
                <MenuItem key={i} value={i}>
                  {toWords.convert(i)}
                </MenuItem>
              ))}
            </Select>
          </div>
        </div>
      )}
      {(gameState === "won" || gameState === "lost") && (
        <Button
          variant="contained"
          size="small"
          onClick={share}
          sx={{
            marginTop: "8px",
            marginBottom: "8px",
            background: "#6aaa64",
            ":hover": {
              background: "#6aaa64",
              opacity: 0.9,
            },
          }}
        >
          Share
        </Button>
      )}
      <div className="spacer"></div>
      {gameState === "new" ? (
        <Button variant="contained" size="small" onClick={start} sx={{ marginTop: "8px", marginBottom: "8px" }}>
          Start
        </Button>
      ) : (
        <Button variant="contained" size="small" onClick={reset} sx={{ marginTop: "8px", marginBottom: "8px" }}>
          Reset
        </Button>
      )}
    </div>
  );
};

type Guess = {
  guessCharacters: GuessCharacter[];
};

type GuessCharacter = {
  character: string;
  status?: CharacterStatus;
};

const GuessesComponent: React.FC<{ guesses: Guess[] }> = ({ guesses }) => {
  return (
    <div className="guesses">
      {guesses.map((guess, i) => (
        <GuessComponent key={`guess_${i}`} guess={guess}></GuessComponent>
      ))}
    </div>
  );
};

const GuessComponent: React.FC<{ guess: Guess }> = ({ guess }) => {
  return (
    <div className="guess_row">
      {guess.guessCharacters.map((guessCharacter, i) => (
        <GuessCharacterComponent key={`guess_character_${i}`} guessCharacter={guessCharacter}></GuessCharacterComponent>
      ))}
    </div>
  );
};

const GuessCharacterComponent: React.FC<{ guessCharacter: GuessCharacter }> = ({ guessCharacter }) => {
  return guessCharacter.character === "" ? (
    <div className="guess_character_div guess_character_empty"></div>
  ) : guessCharacter.status === "unknown" ? (
    <div className="guess_character_div guess_character guess_character_unknown">{guessCharacter.character}</div>
  ) : (
    <div
      className={`guess_character_div guess_character guess_character_${guessCharacter.status} status_${guessCharacter.status}`}
    >
      {guessCharacter.character}
    </div>
  );
};

const KeyboardComponent: React.FC<{
  characterStatusMap: Map<string, CharacterStatus>;
}> = ({ characterStatusMap }) => {
  const rows = [
    ["Q", "W", "E", "R", "T", "Y", "U", "I", "O", "P"],
    ["A", "S", "D", "F", "G", "H", "J", "K", "L"],
    ["Z", "X", "C", "V", "B", "N", "M"],
  ];
  return (
    <div className="keyboard">
      {rows.map((r, i) => (
        <div key={`keyboard_row_${i}`} className="keyboard_row">
          {r.map((ch) => (
            <KeyboardCharacterComponent
              key={`keyboard_character_${ch}`}
              character={ch}
              status={characterStatusMap.get(ch)!}
            ></KeyboardCharacterComponent>
          ))}
        </div>
      ))}
    </div>
  );
};

type CharacterStatus = "unknown" | "not_used" | "wrong_position" | "correct";

const KeyboardCharacterComponent: React.FC<{
  character: string;
  status: CharacterStatus;
}> = ({ character, status }) => {
  return <div className={`keyboard_character status_${status}`}>{character}</div>;
};

export default App;
